import React, { useState } from "react";
import {
  Button,
  Card,
  CardContent,
  TextField,
  Tooltip,
  Typography,
} from "@mui/material";
import VolumeUpIcon from "@mui/icons-material/VolumeUp";
import Meaning from "./Meaning";
import LoadingBar from "react-top-loading-bar";
import LoadingSkeleton from "./LoadingSkeleton";
import Snackbar from "@mui/material/Snackbar";

export default function SearchWord(props) {
  const [response, setResponse] = useState({
    searchedWord: "",
    phonetic: "",
    audio: "",
    meanings: [],
  });
  const { searchedWord, phonetic, audio, meanings } = response;
  const [error, setError] = useState(false);
  const [searchWord, setSearchWord] = useState("");
  const [showMeanings, setShowMeanings] = useState(false);
  const [progress, setProgress] = useState(0);
  const [audioTitle, setAudioTitle] = useState("Listen");
  const [fetching, setFetching] = useState(false);
  const [snackbar, setSnackbar] = useState(false);

  
  const fetchMeaning = (word) => {
    setProgress(30);
    setFetching(true);
    let api = `https://api.dictionaryapi.dev/api/v2/entries/en/${word}`;
    fetch(api)
      .then((res) => {
        setProgress(50);
        return res.json();
      })
      .then((data) => {
        setProgress(60);
        setResponse({
          searchedWord: data[0].word,
          phonetic: data[0].phonetic,
          meanings: data[0].meanings,
          audio:
            data[0].phonetics.length === 0 ? "" : data[0].phonetics[0].audio,
        });
        setAudioTitle("Listen");
        if (data[0].phonetics.length === 0 || data[0].phonetics[0].audio === "")
          setAudioTitle("No Audio");
        setProgress(80);
        setShowMeanings(true);
        setError(false);
        setFetching(false);
        setProgress(100);
      })
      .catch((err) => {
        setError(true);
        setFetching(false);
        setProgress(100);
      });
  };


  const handleClose = () => {
    setSnackbar(false);
  };

  const playAudio = () => {
    if (audio === "") {
      setSnackbar(true);
      return;
    }
    let sound = new Audio(audio);
    sound.play();
  };

  return (
    <div className="search_container">
      <LoadingBar
        color="dodgerblue"
        style={{ zIndex: 1000, position: "fixed", top: 0, height: "4px" }}
        height={3}
        waitingTime={800}
        progress={progress}
        onLoaderFinished={() => setProgress(0)}
      />

      <Snackbar
        anchorOrigin={{ vertical: "top", horizontal: "center" }}
        open={snackbar}
        onClose={handleClose}
        message="No audio found!"
      />
      <Card
        className="search_card"
        sx={{
          padding: 1,
          backgroundColor: props.theme === "light" ? "#f8f8f8" : "#454545",
        }}
      >
        <CardContent>
          <Typography
            sx={{ mb: 2.5 }}
            variant="h3"
            component="div"
            color={props.theme === "light" ? "text.primary" : "#f2f2f2"}
          >
            Search Word
            <p
              style={{
                fontSize: "11px",
                color: props.theme === "light" ? "#222" : "#ddd",
              }}
            >
              <b>
                <i>Source</i>
              </b>
              : Dictionary API
            </p>
          </Typography>
          <div
            className="searchContainer"
            style={{ display: "flex", gap: "2rem", alignItems: "baseline" }}
          >
            <TextField
              sx={{
                mb: 1.5,
                input: {
                  color: props.theme === "light" ? "text.primary" : "#ccc",
                },
              }}
              InputLabelProps={{
                style: { color: props.theme === "light" ? "#1976d2" : "#ccc" },
              }}
              error={error}
              id="standard-error-helper-text"
              label={error ? "Word not found" : "Search here"}
              value={searchWord}
              onChange={(e) => {
                setSearchWord(e.target.value);
              }}
              onKeyPress={(e) => {
                if (e.key === "Enter") {
                  fetchMeaning(searchWord);
                }
              }}
              placeholder="Eg: Hello"
              helperText={error ? "Check your word" : ""}
              variant="standard"
            />
            <Button
              disabled={searchWord === "" ? true : false}
              variant="outlined"
              sx={{ height: "35px" }}
              onClick={() => {
                fetchMeaning(searchWord);
              }}
            >
              Search
            </Button>
          </div>
        </CardContent>
        <LoadingSkeleton isfetching={fetching} />
        <CardContent
          sx={{ marginLeft: 1.5, display: !showMeanings ? "none" : "block" }}
        >
          <div
            className="searchTitle"
            style={{ display: "flex", alignItems: "center" }}
          >
            <Tooltip title={audioTitle}>
              <VolumeUpIcon
                sx={{
                  cursor: "pointer",
                  marginRight: 3,
                  transition: ".1s ease-in",
                  border: "1px solid #ccc",
                  padding: "5px",
                  borderRadius: "50%",
                  color: "dodgerblue",
                  transform: "translateY(-3px)",
                  "&:hover": { backgroundColor: "#ddd" },
                }}
                onClick={() => {
                  playAudio();
                }}
                fontSize="large"
              />
            </Tooltip>
            <Typography sx={{ mb: 1 }} variant="h6" component="h1">
              {searchedWord}
            </Typography>
          </div>
          <Typography
            sx={{ mb: 1, marginLeft: 8 }}
            color={props.theme === "light" ? "text.secondary" : "#ccc"}
          >
            {phonetic}
          </Typography>

          <div className="meanings">
            <ol style={{ listStyle: "decimal", marginLeft: "2rem" }}>
              {meanings.map((meaning, index) => (
                <Meaning key={index} meaning={meaning} theme={props.theme} />
              ))}
            </ol>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
