import React, { useState, useEffect } from "react";
import { Button, Card, CardContent, Typography } from "@mui/material";
import Meaning from "./Meaning";

export default function WordOfTheDay(props) {
  const [word, setWord] = useState("");
  const [isSummary, setIsSummary] = useState(true);
  const [isloading, setIsloading] = useState(true);
  const [wordAssets, setWordAssets] = useState({
    searchedWord: "",
    partOfSpeech: "",
    definition: "",
    example: "",
    meaning: [],
  });

  let meaningApi = `https://api.dictionaryapi.dev/api/v2/entries/en/${word}`;
  const fetchRandomWord = async () => {
    const savedWord = JSON.parse(localStorage.getItem("savedWord"));
    const savedDate = JSON.parse(localStorage.getItem("savedDate"));

    if (savedWord === null) {
      let api = `https://random-word-api.herokuapp.com/word?number=1`;
      let getRanWord = await fetch(api);
      let data = await getRanWord.json();
      setWord(data[0]);
      localStorage.setItem("savedWord", JSON.stringify(data[0]));
      localStorage.setItem("savedDate", JSON.stringify(new Date()));
    } else {
      let today = new Date();
      let savedDay = new Date(savedDate);
      if (today.getDate() !== savedDay.getDate()) {
        let api = `https://random-word-api.herokuapp.com/word?number=1`;
        let getRanWord = await fetch(api);
        let data = await getRanWord.json();
        setWord(data[0]);
        localStorage.setItem("savedWord", JSON.stringify(data[0]));
        localStorage.setItem("savedDate", JSON.stringify(new Date()));
        // console.log('localstorage is not empty but the date is different so we fetched a new word', word);
      } else {
        setWord(savedWord);
        // console.log('localstorage is not empty and the date is the same so we used the saved word', word);
      }
    }
  };

  useEffect(() => {
    fetchRandomWord();
  }, []);

  async function fetchWord(api) {
    setIsloading(true);
    let getRespnose = await fetch(api);
    getRespnose
      .json()
      .then((data) => {
        setWordAssets({
          searchedWord: data[0].word,
          partOfSpeech: data[0].meanings[0].partOfSpeech,
          definition: data[0].meanings[0].definitions[0].definition,
          example: data[0].meanings[0].definitions[0].example,
          meaning: data[0].meanings,
        });
        setIsloading(false);
      })
      .catch((err) => {
        console.log(err);
        fetchRandomWord();
      });
  }
  useEffect(() => {
    fetchWord(meaningApi);
  }, [word]);

  const handleToggle = () => {
    setIsSummary(!isSummary);
  };
  return (
    <div className="word-of-the-day_container">
      <Card
        sx={{
          padding: 0,
          mb: "auto",
          backgroundColor: props.theme === "light" ? "#f8f8f8" : "#454545",
          height: "100%",
        }}
      >
        <CardContent className="desktopView">
          <Typography variant="body1" component="div" color="primary.main">
            word of the Day
          </Typography>

          <Typography
            sx={{ mb: 1.5 }}
            variant="h4"
            component="div"
            color={props.theme === "light" ? "text.primary" : "#ddd"}
          >
            {wordAssets.searchedWord}
          </Typography>

          <div
            className="summary"
            style={{ display: isSummary ? "block" : "none" }}
          >
            <Typography
              sx={{ mb: 1, fontStyle: "italic" }}
              color={props.theme === "light" ? "text.secondary" : "#ccc"}
            >
              {wordAssets.partOfSpeech}
            </Typography>
            <Typography
              sx={{ mb: 0.2, fontSize: "1rem", marginLeft: "1rem" }}
              variant="body2"
              color={props.theme === "light" ? "text.primary" : "#ddd"}
            >
              {wordAssets.definition}
            </Typography>
            <Typography
              sx={{ mb: 1.5, fontSize: ".9rem", marginLeft: "2.5rem" }}
              color={props.theme === "light" ? `text.secondary` : "#aaa"}
            >
              {wordAssets.example === undefined ? "" : wordAssets.example}
            </Typography>

            <Button
              sx={{ display: isloading ? "none" : "block" }}
              onClick={handleToggle}
              variant="outlined"
              color="primary"
            >
              Show More
            </Button>
          </div>
        </CardContent>
        <CardContent
          className="fullMeaning"
          sx={{ paddingTop: "0 !important", transform: "translateY(-16px)" }}
        >
          <div
            className="full-meaning"
            style={{ display: isSummary ? "none" : "block" }}
          >
            <ol style={{ listStyle: "decimal" }}>
              {wordAssets.meaning.map((meaning, index) => (
                <Meaning key={index} meaning={meaning} theme={props.theme} />
              ))}
            </ol>
            <Button
              sx={{ display: isloading ? "none" : "block" }}
              onClick={handleToggle}
              variant="outlined"
              color="primary"
            >
              Show Less
            </Button>
          </div>
        </CardContent>
      </Card>
    </div>
  );
}
